@import url(https://fonts.googleapis.com/css?family=Ubuntu:700);
@font-face {
  font-family: CamingoCode-Reg;
  src: url(/static/media/CamingoCode-Regular.cb296dd6.ttf); }

@font-face {
  font-family: CamingoCode-Bold;
  src: url(/static/media/CamingoCode-Bold.d30c1423.ttf); }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  line-height: 1.5rem;
  color: #DBDBDB; }

body,
html,
main {
  background-color: #201f1f;
  width: 100%;
  height: 100%;
  font-family: CamingoCode-Reg;
  font-size: .94rem;
  scroll-behavior: smooth; }

h1 {
  color: #005bd1; }

h2 {
  color: #005bd1; }

h3 {
  color: #005bd1; }

h4 {
  color: #005bd1; }

h5 {
  color: #005bd1; }

h6 {
  color: #005bd1; }

h1 {
  font-family: 'Ubuntu', sans-serif;
  font-size: 3.25rem;
  text-align: center;
  margin-bottom: 2rem; }
  h1 * {
    color: #005bd1; }

h2 {
  font-family: 'Ubuntu', sans-serif;
  font-size: 3rem;
  color: #005bd1;
  margin-bottom: 2rem;
  margin-left: 5rem; }

h3 {
  font-size: 1.5rem; }

.text-code,
.bold {
  font-family: CamingoCode-Bold; }

.blue {
  color: #005bd1; }

.red {
  color: #ff6262; }

.green {
  color: #1ad129; }

code {
  font-family: CamingoCode-Reg; }

@-webkit-keyframes arrowAnimation {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes arrowAnimation {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

.container {
  padding: 3.25rem;
  width: 100%;
  min-height: 100vh; }

[class*="flex"] {
  display: flex; }

.flex-center {
  justify-content: center;
  align-items: center; }

.flex-around {
  justify-content: space-around;
  align-items: center; }

.flex-center-vertical {
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.content {
  padding: 2rem 1rem; }

.grid-container {
  display: grid; }

.home {
  width: 100%;
  height: 100vh;
  background-color: #201f1f;
  color: #005bd1;
  position: relative; }
  .home a {
    z-index: 1;
    position: absolute;
    bottom: 0; }

.particles {
  width: 100%;
  height: 100%;
  position: absolute; }

#About_Me {
  position: relative; }
  #About_Me img {
    -webkit-filter: hue-rotate(185deg);
            filter: hue-rotate(185deg); }
  #About_Me .content > * {
    width: 40%; }

#Services .content {
  -webkit-perspective: 1000px;
          perspective: 1000px;
  padding-top: 2rem; }

.service-container {
  width: 25%;
  position: relative;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: 0.6s;
  transition: 0.6s;
  border: 1px #005bd1 solid; }
  .service-container img {
    width: auto;
    height: 300px;
    margin-bottom: 3rem; }
  .service-container .front {
    height: 100%;
    z-index: 2;
    width: 100%;
    padding: 1rem;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden; }
  .service-container .back {
    position: absolute;
    text-align: center;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    height: 100%;
    width: 100%;
    padding: 1rem;
    overflow: auto;
    color: #DBDBDB;
    background-color: #201f1f;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden; }
  .service-container:hover {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg); }

#Code .grid-container {
  width: 100%;
  height: 100%;
  padding: 2rem;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 1fr; }

#Code .list {
  grid-column: span 3; }
  #Code .list__section__name {
    margin-bottom: .75rem;
    font-size: 1.25rem;
    color: #201f1f; }
  #Code .list__section__element.active {
    color: #005bd1; }
  #Code .list__section__element:hover {
    cursor: pointer; }
  #Code .list ul {
    margin-left: 2rem; }

#Code .code-item {
  grid-column: span 9;
  grid-template-columns: repeat(12, 1fr);
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
  opacity: 0; }
  #Code .code-item.appear {
    opacity: 1;
    -webkit-transition: opacity 0.2s;
    transition: opacity 0.2s; }
  #Code .code-item.hide {
    opacity: 0;
    -webkit-transition: opacity 0.2s;
    transition: opacity 0.2s; }
  #Code .code-item__icon {
    grid-column: span 8; }
    #Code .code-item__icon__level {
      height: auto;
      margin-bottom: 1rem; }
    #Code .code-item__icon__image {
      height: 80%; }
  #Code .code-item__description {
    grid-column: span 4;
    padding: 2rem; }

#Contact .console {
  background-color: rgba(219, 219, 219, 0.1);
  padding: 1.8rem;
  width: 50%;
  margin-left: 2rem; }
  #Contact .console .input-group {
    margin-bottom: 2px;
    width: 100%; }
    #Contact .console .input-group * {
      vertical-align: top; }
  #Contact .console input,
  #Contact .console textarea {
    resize: none;
    width: 80%;
    font-family: CamingoCode-Reg;
    background-color: rgba(219, 219, 219, 0.1);
    border: 0;
    color: #dbdbdb;
    padding: 1px 5px; }
  #Contact .console textarea {
    height: 6rem; }

footer {
  background-color: rgba(219, 219, 219, 0.1);
  width: 100%;
  padding: 3rem;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 4rem 1rem; }
  footer * {
    color: #005bd1; }
  footer .brand-container,
  footer .navigation,
  footer .social {
    grid-column: span 4;
    font-size: 1.2rem; }
  footer .logo {
    margin: 0 auto 1rem;
    max-height: 200px; }
  footer .navigation__container {
    width: 100%;
    height: 100%;
    list-style: none; }
    footer .navigation__container__element {
      margin: 0 auto 1rem; }
      footer .navigation__container__element__link {
        color: #005bd1;
        text-decoration: none; }
        footer .navigation__container__element__link:hover {
          cursor: pointer; }
  footer .social__links {
    width: 100%; }
  footer .social h4 {
    margin-bottom: 2rem; }
  footer .social img {
    width: 70%;
    margin: 0 auto; }
  footer .credits {
    grid-column: span 12; }

.Typist .Cursor {
  display: inline-block; }
  .Typist .Cursor--blinking {
    opacity: 1;
    -webkit-animation: blink 1s linear infinite;
            animation: blink 1s linear infinite; }

@-webkit-keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.arrowDownIcon svg,
.arrowUpIcon svg {
  height: 7rem; }

.pointIcon svg {
  height: 1.1rem;
  margin-bottom: 6.9rem; }

.arrowDownLink {
  position: absolute;
  bottom: 0; }
  .arrowDownLink .triangle {
    -webkit-transition: all ease .5s;
    transition: all ease .5s; }
  .arrowDownLink:hover .triangle {
    -webkit-animation: arrowAnimation .7s ease infinite;
            animation: arrowAnimation .7s ease infinite; }

header {
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 20; }
  header nav {
    position: absolute;
    right: 0;
    padding: 2rem 4rem; }
    header nav ul {
      list-style: none; }
    header nav .nav-link {
      color: #005bd1;
      display: block;
      margin: 5px;
      text-decoration: none;
      font: 1.25rem CamingoCode-Bold;
      -webkit-transition: all .5s ease;
      transition: all .5s ease; }
    header nav [class*='bracket'] {
      -webkit-transition: all ease .6s;
      transition: all ease .6s; }
    header nav:hover {
      cursor: pointer; }
      header nav:hover .point-1 {
        -webkit-animation: bounce 1s ease 0ms infinite;
                animation: bounce 1s ease 0ms infinite; }
      header nav:hover .point-2 {
        -webkit-animation: bounce 1s ease 80ms infinite;
                animation: bounce 1s ease 80ms infinite; }
      header nav:hover .point-3 {
        -webkit-animation: bounce 1s ease 160ms infinite;
                animation: bounce 1s ease 160ms infinite; }
      header nav:hover [class*='bracket'] {
        -webkit-transition: all ease 1s;
        transition: all ease 1s; }
      header nav:hover .bracket-open {
        -webkit-transform: translateX(-15px);
                transform: translateX(-15px);
        -webkit-transition: all ease .6s;
        transition: all ease .6s; }
      header nav:hover .bracket-close {
        -webkit-transform: translateX(15px);
                transform: translateX(15px);
        -webkit-transition: all ease .6s;
        transition: all ease .6s; }
    header nav .bracketIcon {
      height: 35px; }
    header nav .pointIcon {
      height: 12px;
      margin: 1px; }
    header nav.open .menuIcon {
      opacity: 0;
      width: 0;
      -webkit-transition: all .1s ease;
      transition: all .1s ease; }
    header nav.open .menuArray {
      opacity: 1;
      width: 430px;
      -webkit-transition: all 1s ease;
      transition: all 1s ease;
      overflow: hidden; }
    header nav.closed .nav-link {
      margin: 0;
      -webkit-transition: all 1s ease;
      transition: all 1s ease; }
    header nav.closed .menuArray {
      width: 0;
      opacity: 0;
      -webkit-transition: all .8s ease;
      transition: all .8s ease;
      overflow: hidden; }
    header nav.closed .menuIcon {
      opacity: 1;
      width: auto;
      -webkit-transition: all .5s ease 1s;
      transition: all .5s ease 1s; }

